import React, { Component } from 'react';
import Particles from 'react-particles-js';
import Title from '../components/Title';
import Messages from '../components/Messages';
import Refresh from '../components/Refresh';
import Footer from '../components/Footer';
import Tilt from 'react-tilt';
import '../containers/App.css';

class App extends Component {
  constructor() {
    super();
    this.state = {
      content: null,
    };
  }

  componentDidMount() {
    fetch('https://api.kanye.rest/')
      .then((resp) => resp.json())
      .then((data) => this.setState({ content: data.quote }));
  }

  quoteOnClick = () => {
    fetch('https://api.kanye.rest/')
      .then((resp) => resp.json())
      .then((data) => this.setState({ content: data.quote }));
  };

  render() {
    return (
      <div className='min-vh-100'>
        <Particles
          className='particles'
          params={{
            particles: {
              number: {
                value: 100,
                density: {
                  enable: true,
                  value_area: 800,
                },
              },
            },
          }}
        />
        <div className='flex items-center justify-center tc pa4 ma4'>
          <Tilt className='Tilt ' options={{ max: 40 }}>
            <div className='Tilt-inner bg-washed-green mw5 pa3 br4 grow h-auto'>
              <Title />
              <Messages message={this.state.content} />
            </div>
          </Tilt>
        </div>
        <Refresh quoteOnClick={this.quoteOnClick} />
        <Footer />
      </div>
    );
  }
}

export default App;
