import React from 'react';
import icon from './refresh.png';

const Refresh = ({ quoteOnClick }) => {
  return (
    <div className='bg-transparent flex items-center justify-center pa2'>
      <input
        style={{ outline: 'none' }}
        type='image'
        img
        className='w2'
        src={icon}
        alt='refresh'
        onClick={quoteOnClick}
      />
    </div>
  );
};

export default Refresh;
